@import url('https://fonts.googleapis.com/css2?family=Montserrat:400,500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Caladea&display=swap');
@import "_PaintBX_variables";
@import "_PaintBX_titles";

* {
    box-sizing: border-box;
}

a {
    text-decoration: none;
    color: inherit;
    @extend %miniTitle;
}
li {
    list-style: none;
}

body {
    font-family: "Caladea";
    padding-top: 70px;
    & > *:not(.footer) {
        padding: 0px 140px;
    }
}
header {
    z-index: 1;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    padding: 0px 140px;
    background-color: white;
    height: 70px;
    display: flex;
    align-items: center;
    nav {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        ul {
            display: flex;
            text-transform: uppercase;
            font-size: 10px;
            &:first-child{
                li {
                    margin-right: 60px;
                }
            }
        }
    }
}

.hero {
    height: 100vh;
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
     url("https://images.unsplash.com/photo-1583248369069-9d91f1640fe6?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1352&q=80");
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    padding: 0;
    margin: 0px $sideSpace;
    h4 {
        @extend %miniTitle;
        margin-bottom: 20px; 
    }

    h3 {
        font-size: 40px;
        width: 35%;
        text-align: center;
        margin-bottom: 50px;
    }
    a {
        width: 10%;
        background-color: white;
        padding: 20px 0px;
        text-align: center;
        text-decoration: none;
        color: black;
        @extend %miniTitle;
        font-size: 10px;
        transition: color .2s linear, background 0.2s linear;
        &:hover {
            background-color: black;
            color: white;
        }
    }
}

.under-hero {
    height: 80vh;
    display: flex;
    width: 100%;
    margin-bottom: 80px;
    .under-hero__content {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: $bg;
        .wrapper {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            h4 {
                @extend %miniTitle;
                margin-bottom: 30px;
            }
            h3 {
                font-size: 36px;
                text-align: center;
                margin-bottom: 50px;
            }
            a {
                border: 1px solid black;
                padding: 20px;
            }
        }
    }
    img,
    div {
        width: 50%;
    }
}

.blog {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 60vh);
    margin-bottom: 80px;
    .blog__post {
        background-color: $bg;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        &:nth-child(even) {
            img {
                order: 1;
            }
        }
        img {
            width: 100%;
            height: 100%;
        }
        .post__content {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .post__date {
                @extend %miniTitle;
                margin-bottom: 20px;
            }
            h4 {
                font-size: 26px;
            }
        }
    }
}

.gallery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 306px);
    .gallery__poster {
        height: 100%;
        cursor: pointer;
        img {
            max-width: 100%;
            height: 100%;
            transition: opacity 0.3s linear;
            &:hover {
                opacity: 0.5;
            }
        }
    }
}

.footer {
    margin-top: 100px;
    background-color: $bg;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 65px 0px;
    .footer__column {
        display: flex;
        flex-direction: column;
        align-items: center;
        @extend %miniTitle;
        .column__title {
            margin-bottom: 50px;
            opacity: 0.5;
        }
        ul{
            text-align: center;
            li {
                margin-bottom: 15px;
            }
        }
        &:nth-child(2){
            border-right: 1px solid black;
            border-left: 1px solid black;
        }
    }
}